nav{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 18%;
  height: 100%;
  background: #d8ecf3;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  margin-top: 50px;
  color: #193e4b;
  font-size: 170%;
}

.headshot {
  margin-top: 20px;
  border-radius: 15%;
  width: 50%;
}

nav>ol{
  margin-top: 70px;
  height: 80%;
  display: flex;
  flex-direction: column;
}

.menu-item{
  position: relative;
  height: 10%;
  display: flex;
  align-items: center;
}

.menu-item:after{
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: 0;
  width: 0;
  z-index: -1;
  box-shadow: 0 0 0 0 #1e1e1e;
  transition: .2s all linear;
}

.menu-item:nth-child(1):after{
  background: #9ed1e1;
}

.menu-item:nth-child(2):after{
  background: #9ed1e1;
}

.menu-item:nth-child(3):after{
  background: #9ed1e1;
}

.menu-item:nth-child(4):after{
  background: #9ed1e1;
}

.menu-item:nth-child(5):after{
  background: #9ed1e1;
}

.menu-item:hover:after{
  width: 100%;
}

.menu-item>a, .submenu-item>a{
  color: #193e4b;
  text-decoration: none;
}

.nav-item {
  margin-left: 15px;
}

.logos {
  margin-top: -220px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
